.card-container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  grid-template-rows: repeat(2, 406px);
  justify-content: center;
  gap: 48px 46px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .card-container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .card-container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .card-container {
    --column-count: 4;
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__img {
  width: 160px;
  height: 134px;
  padding: 32px 19px 23px;
}

.card__description {
  margin-top: 5px;
  padding: 0 16px;
}

.card__description p:first-child {
  margin-bottom: 0;
  line-height: 18px;
}

.card__description p:last-child {
  color: #616070;
  margin-top: 5px;
  margin-bottom: 9px;
  font-size: 10px;
  font-weight: 400;
}

.card__stars {
  justify-content: space-between;
  margin-top: 9px;
  padding: 0 16px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  margin: 27px 0 19px;
  padding: 0 16px;
  display: flex;
}

.card__price div:first-child {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 11px;
}

.card__price div:last-child {
  vertical-align: baseline;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 12px;
}

.card__button {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border-radius: 5px;
  margin: 0 16px 16px;
  padding: 12px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 2px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star:first-child {
  margin-left: 0;
}

.card__stars p:last-child {
  height: 16px;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 18px;
}

.navbar {
  justify-content: space-between;
  align-items: center;
  height: 100%;
  margin: auto;
  padding: 0 50px;
  display: flex;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-list__item {
  margin-right: 20px;
  list-style-type: none;
}

.nav-list__item:last-child {
  margin-right: 0;
}

.nav-list__link {
  color: #060b35;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav-list__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 4px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

body {
  margin: 0;
  font-family: Roboto, serif, sans-serif;
  font-size: 12px;
  font-weight: 500;
}
